@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
  --theme-color: #BD59FF;
  --theme-dark: #2F1640;
  --theme-light: #BD59FF;
  --theme-alt-one: #8E43BF;
  --theme-alt-two: #ecd0ff;
}

* {
  padding:0;
  margin:0;
}

html, body {
  background-color: #8E43BF;
  background-color: var(--theme-alt-one);
  overflow-x: hidden;
}

#root {
  background-color: #2F1640;
  background-color: var(--theme-dark);
}

body {
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.home {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    min-width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    -webkit-align-items: center;
            align-items: center;
    height: 90vh;
    margin-top:2vh;
}

.home .title {
    width: 100%;
    font-size: 18px;
    text-align: center;
}

.home .title h1 {
    font-weight: 300;
}

.home .title p {
    line-height: 50px;
}

.home .title p:nth-child(3) {
    color: darkslategrey;
    text-decoration: underline;
    text-underline-offset: 5px;
    font-size: 2rem;
    letter-spacing: 2px;
}

.home .person {
    width: 100%;
    margin-top: 20px;
}

.home .person img {
    width: 80%;
    -webkit-filter:drop-shadow(0 0 2px var(--theme-alt-one));
            filter:drop-shadow(0 0 2px var(--theme-alt-one));
}

@media (min-width: 800px) {
    .home {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        margin-top:0;
    }
    .home .title,
    .home .person {
        width: 50%;
        margin-top: 0;
        text-align: left;
        font-size: 25px;
    }
    .home .person img {
        width: 60%;
    }
}

header {
    width: 100%;
    padding: 10px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 1fr;
    background-color: var(--theme-color);
    /* margin-bottom: 10vh; */
    /* border-radius: 25px 25px 0 0; */
}

header div {
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.contact {
    text-align: center;
    vertical-align: middle;
    border: 5px double;
    border-color: var(--theme-color);
    border-right-color: var(--theme-dark);
    border-radius: 50px;
    color: white;
    text-shadow: 2px 2px 5px var(--theme-dark);
}

.circle-img img {
    width: 100px;
    height: 30px;
    border-radius: 20px;
    box-shadow: 0 2px 5px var(--theme-dark);
}

@media (min-width: 800px) {
    header {
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: 1fr;
    }
}

.projects * {
    z-index:1 !important;
    position: relative;
}

.projects {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    position: relative;
    height: 100%;
    margin: 5vh 0;
    z-index:0;
}

.projects h1 {
    margin: 40px 0;
    border-bottom: 2px double var(--theme-light);
    font-weight: 400;
    font-style: italic;
    color: var(--theme-dark);
}

.navbar {
    position: fixed;
    width: 5%;
    height: 90%;
    left: 5%; /* required */
    bottom: 0;
    font-size: 0.75rem;
    text-align: center;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: var(--theme-dark);
    border-top: inset 20px var(--theme-color);
}

.project-title {
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--theme-light);
    margin: 10px;
    padding: 5px;
    border-radius:15px;
}

ul {
    list-style: none;
}

@media (min-width: 768px) {
    .navbar {
        left:0;
    }
}
.app {
  display:-webkit-flex;
  display:flex;
  margin: 0 40px;
  max-width: 1280px;
  padding: 0 15px;
  min-height: 100vh;
  border-radius: 25px;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: var(--theme-alt-two);
}

#page {
  display:none;
}

#intro {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

@media (min-width: 800px){
  .app {
    padding: 0 40px;
    margin-top: 5vh;
    min-height: 90vh;
  }
}

